.schedule-button-wrapper{
  width: 75%;
  display: flex;
  justify-content: center;
}

.schedule-button{
  width: 100%;
  border-radius: 8px;
  margin: 15px 0 15px 0;
  /*border: #1665D8 solid;*/
  box-shadow: 0px 0px 0px 2px rgba(239,245,254,1);
  border: 2px solid #C1C1C1;
  padding: 5px 0px 5px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  font-size: 18px;
  user-select: none;
  pointer-events: none;
}

.schedule-button-active{
  cursor: pointer;
  user-select: none;
  background-color: #1665D8;
  border: 2px solid #1665D8;
  transition: all 0.2s;
  pointer-events: all;
}

.schedule-button-active:hover{
  transition: all 0.2s;
  background-color: #0f3e8b;
  border: 2px solid #0f3e8b;
}

.schedule-button-text{
  padding: 18px 55px 18px 55px;
  color: #FFFFFF;
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  font-size: 20px;
  transition: all 0.2s;
}

.schedule-submit-button:hover .schedule-button-text{
  color: white;
  transition: all 0.2s;
}

.schedule-submit-button:hover{
  background-color: #0f3e8b;
  transition: all 0.2s;
}