.cta-section {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(47, 62, 131);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
    opacity: 0;
    animation: fadeIn 0.2s linear forwards;
}

.cta-form-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.top-logo {
    width: 25%
}

.demo-cta-header {
    font-size: 28px;
    margin-bottom: 0;
}

.contact-field {
    -webkit-text-size-adjust: 100%;
    color: #333;
    font-size: 14px;
    line-height: 15px;
    box-sizing: border-box;
    display: block;
}

.email-input-video-cta {
    -webkit-text-size-adjust: 100%;
    box-sizing: border-box;
    font: inherit;
    margin: 0;
    width: 100%;
    color: #333;
    vertical-align: middle;
    background-color: #fff;
    padding: 4px 8px;
    height: 45px;
    border: 1px solid #ccc;
    margin-bottom: 0;
    font-size: 17px !important;
    line-height: 16px;
    transition: all .2s;
    display: block;
    border-radius: 5px;
}

.video-cta-form {
    width: 50%
}

.sign-btn-box {
    justify-content: center;
    align-items: center;
    display: flex;
}

.video-form-button {
    -webkit-text-size-adjust: 100%;
    box-sizing: border-box;
    font: inherit;
    margin: 0;
    line-height: inherit;
    border: 0;
    color: #fff;
    align-items: center;
    font-size: 17px;
    text-decoration: none;
    display: flex;
    -webkit-appearance: button;
    cursor: pointer;
    background-color: #01cc74;
    padding-left: 36px;
    padding-right: 36px;
    font-weight: 400;
    transition: all .2s;
    width: 100%;
    height: 45px;
    text-align: center;
    justify-content: center;
    margin-top: 10px;
    border-radius: 5px;
}

.video-form-button:hover {
    background-color: #007241;

}

@keyframes fadeIn {
    0% {
        opacity: 0%;
    }
    100% {
        opacity: 100%;
    }
}


.video-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    background-color: rgba(0, 0, 0, 0.9);
}

.video-container > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.email-input-video-cta:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(81, 203, 238, 1); /* Replace color with your desired color */
}


