.calculator-results-wrapper {
    padding: 50px;
}

.calculator-results-section-heading {
    font-weight: 600;
    font-size: 22px;
    color: #344054;
    margin-bottom: 5px;
}

.calculator-results-section-value {
    font-weight: 400;
    font-size: 24px;
    color: #344054;
    margin-top: 5px;
}

table, th, td {
    font-family: 'Poppins', sans-serif;
    border: 1px solid #333;
    border-collapse: collapse;
    margin-bottom: 26px;
    text-align: center;
}

th, td {
    padding: 10px;
}

table {
    border-collapse: collapse;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    font-family: sans-serif;
    border-radius: 7px;
    overflow: hidden;
    border-spacing: 0;
    border: transparent;
}

tr th {
    background-color: #1665D8;
    color: #ffffff;
    text-align: center;
    border: none;
}

th,
td {
    padding: 12px 18px;
    border: none;
}

td {
    color: #333333;
    border: none;
}

tr {
    border-bottom: 1px solid #dddddd;
    border: none;
}

tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.heading-margin-bottom {
    margin-bottom: 25px;
}