.calculator-body-number-of-units-component {
    display: grid;
    grid-template-columns: 0.85fr 0.15fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    width: 100%;
    align-items: center;
}

.calculator-wrapper {
    margin-top: 0px;
    width: 835px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #ffffff;
}

.calculator-body {
    padding: 0px;
}

#numOfUnits::-webkit-inner-spin-button,
#numOfUnits::-webkit-outer-spin-button {

    opacity: 1 !important;
    display: inline-block;
    -webkit-appearance: inner-spin-button;

}

.calculator-body-number-of-units-component input {
    justify-self: center;
    width: 90%;
}

.numberInputWithScroll {
    text-align: center;
    font-size: 22px !important;
    border-radius: 7px;
    overflow: hidden;
    border: rgb(22, 101, 216) solid 1px;
    /*outline-color: #1665D8;*/
    /*border-color: #1665D8;*/
    /*box-shadow: 0px 0px 0px 2px rgba(239,245,254,1);*/
}

.numberInputWithScroll:focus-visible {
    border-color: #1665D8;
    outline-color: #1665D8;
}

.css-1uvydh2 {
    padding: 10px !important;
}

input[type="range"] {
    -webkit-appearance: none;
    height: 7px;
    background: rgb(7, 52, 126);
    border-radius: 5px;
    background-image: linear-gradient(#1665D8, #1665D8);
    background-repeat: no-repeat;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: #1665D8;
    cursor: pointer;
    box-shadow: 0 0 2px 0 #1665D8;
    transition: background 0.3s ease-in-out;
}

input[type="range"]::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
}

input[type="range"]::-webkit-slider-thumb:hover {
    box-shadow: rgba(22, 101, 216, 0.5) 0px 0px 0px 8px;
}

input[type="range"]::-webkit-slider-thumb:active {
    box-shadow: rgba(22, 101, 216, 0.5) 0px 0px 0px 11px;
    transition: box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    left 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    bottom 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

h3 {
    font-weight: 600;
    font-size: 19px;
    color: #344054;
    text-align: center;
}

.calculator-field-label {
    margin: 0 0 10px 0;
    user-select: none;
}

.monthly-yearly-check {
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 15px;
    align-items: center;
}

.monthly-yearly-check-label {
    font-size: 18px;
}

.left-right-padding {
    padding: 0 50px 0 50px;
}

@media only screen and (max-width: 700px) {
    .calculator-two-column-layout {
        display: flex;
        flex-direction: column;
    }

    .calculator-body-number-of-units-component {
        width: 90% !important;
    }
}