.calculator-section-heading {
    display: inline-block;
    position: relative;
    margin-bottom: 35px;
    user-select: none;
}

.calculator-wrapper {
    margin-top: 0px;
    width: 835px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #ffffff;
}

.calculator-header {
    background-color: #2F3E83;
    color: white;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.calculator-body {
    padding: 0px;
}

.calculator-header-text {
    padding: 15px 50px 15px 50px;
}


.calculator-body {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.calculator-body-snippet {
    width: 75%;
    text-align: center;
    align-self: center;
    color: gray;
    line-height: 20px;
}

.paystubs-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.centered {
    text-align: center;

}

.paystub-headers-container {
    width: 100%;
    border-radius: 8px;
    display: grid;
    grid-template-columns: 3fr 3fr 3fr 3fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.css-1uvydh2 {
    padding: 10px !important;
}





