.quizResultWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 0px;
    align-items: center;
    text-align: center;
}

.quiz-result-header-image {
    width: 25%;
    margin-bottom: 25px;
}

.quiz-results-title-text {
    font-size: 32px;
    margin-bottom: 45px;
    color: #000000;
    user-select: none;
    font-weight: 600;
}

.quizResultWrapper h3 {
    font-size: 24px;
    margin-bottom: 30px;
    user-select: none;
    color: #162050;
    font-weight: 500;
    transition: all 0.2s;
}

.quizResultWrapper h3:hover {
    color: #1665D8;
    transition: all 0.2s;
}

.quiz-results-form {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.quiz-demo-email {
    width: 75%;
    display: flex;
    justify-content: center;
}

.email-form-icon {
    margin-right: 5px;
    margin-top: 4px;
    margin-left: 15px;
}

