.quizQuestionAnswerChoiceWrapper{
    width: 100%;
    border-radius: 8px;
    margin: 15px 0 15px 0;
    /*border: #1665D8 solid;*/
    box-shadow: 0px 0px 0px 2px rgba(239,245,254,1);
    border: 2px solid #C1C1C1;
    padding: 15px 0px 15px 0px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s;
    font-size: 18px;
    user-select: none;
}

.quizQuestionAnswerChoiceWrapper:hover{
    border: 2px solid #1665D8;
    outline: none;
    background-color: #1665D8;
    transition: all 0.2s;
}

.quizQuestionAnswerChoiceWrapper:hover .quizQuestionAnswerText{
    color: #FFFFFF;
    transition: all 0.2s;
}

.quizQuestionAnswerText{
    transition: all 0.2s;
}

.quizQuestionAnswerChoice{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #C1C1C1;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: #FFFFFF;
    margin: 5px 20px 5px 20px;
    font-weight: bold;
}

.activeQuizQuestion{
    background-color: #1665D8;
    border: 2px solid #1665D8;
    outline: none;
}

.activeQuizQuestion span{
    color: #FFFFFF
}